/* ---base rules--- */
body,
button,
input,
select {
    font-family: "Nunito Sans", sans-serif;
}

* {
    color: white;
}

div{
    svg{max-inline-size: 100%;}
    
    img{max-inline-size: 100%;}
}

ul {
    list-style-type: none;
    padding-left: 0;
}


/* ---layout--- */
.header,
.main {
    padding: 0 5vw;
}


/* ---modules---- */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__theme__switch {
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-width: 110px;
    }
        
    &__theme__icon {
        max-width: 20px;
    }
}


.countriesSearchPage section {
    margin-top: 30px;
}

.countriesList {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    justify-items: center;
}


/* ---theme--- */
body {
    background-color: hsl(207, 26%, 17%);
}

.header,
input,
select
 {
    background-color: hsl(209, 23%, 22%);
}

.header__theme {
    &__switch {
        background-color: transparent;
        border: none;
    }

    &__icon {
        fill: white
    }
}